.header__logo {
  position: relative;
  left: 0%;
  top: 0%;
  max-width: 173px;
  min-height: 74px;
}

@media screen and (max-width: 880px) {
  .header__logo {
    margin-left: 27px;
  }
}

@media screen and (max-width: 450px) {
  .header__logo {
    max-width: 126px;
    min-height: 56px;
  }
}
